import React from 'react';
import '../css/projects/cardpresent.css'
import Arduino from '../photos/logo/arduino.png';
import Reactt from '../photos/logo/react.png';

function CardPresent(props) {



    return (
        <div>
            {
                (props.Tags.includes(props.selectTag) || props.selectTag == 'all') ?

                    < div tag={props.Tags} className="cardpresent">
                        <div className="logos-list">
                            <img src={Arduino} className="logos" />
                            <img src={Reactt} className="logos" />
                        </div>

                        <div className="photo-text">
                            <img src={Arduino} />
                            <p>some random text here</p>
                        </div>

                    </div >
                    : null
            }
        </div>

    );





}

export default CardPresent;